@import url(https://fonts.googleapis.com/css?family=Roboto:400,700&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  font-size: 14px;
  color: rgba(0, 0, 0, 0.8);
  background: rgb(250, 250, 250);
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

nav{
    min-width: 288px;
    height: 100%;
    padding: 0 24px;
    background: #fff;
    box-shadow: 0px 8px 20px 0px rgba(0, 0, 0, 0.16);
    position: fixed;
    left: 0;
    top: 0;
    z-index: 1;
    -webkit-transition: ease 0.26s all;
    transition: ease 0.26s all;
}
nav h3{
    margin: 32px 0 24px 0;
    color: #000;
    font-weight: normal;
}
nav a{
    display: flex;
    padding: 10px 16px;
    align-items: center;
    margin: 4px 0;
    color: rgba(0, 0, 0, 0.8);
    font-size: 14px;
}
nav i{
    margin-right: 16px;
    color: #8bc34a;
}
nav a.active{
    background: rgba(225, 245, 254, 0.8);
    font-weight: bold;
}
nav a:hover{
    background: rgba(225, 245, 254, 0.8);
}
.menu{
    display: none;
}
@media only screen and (max-width: 1000px){
    nav{
        left: -288px;
        box-shadow: none;
    }
    .openNav{
        left: 0;
        box-shadow: 0px 8px 20px 0px rgba(0, 0, 0, 0.16);
    }
    .openNavMenu span{
        width: 13px !important;
    }
    .menu{
        position: absolute;
        top: 12px;
        right: 12px;
        cursor: pointer;
        display: none;
        background: transparent;
        border: none;
        outline: none;
      }
      .menu span{
        display: block;
        height: 3px;
        background: #fff;
        width: 20px;
        margin: 2px 0;
        -webkit-transition: ease 0.3s all;
        transition: ease 0.3s all;
        margin-left: auto;
      }
      .menu .halfLine{
        width: 12px;
      }
}
@media only screen and (max-width: 600px){
    nav{
        min-width: 276px;
        padding: 0 20px !important;
    }
    nav i{
        margin-right: 12px;
        color: #8bc34a;
    }
    nav a{
        padding: 9px 12px;
    }
}
*{
  outline: none;
  font-family: 'Roboto', sans-serif;
  box-sizing: border-box;
}
a{
  color: inherit;
  text-decoration: none;
}
.addBtn{
  position: fixed;
  bottom: 16px;
  right: 16px;
  color: #fff;
  background: #4caf50;
  padding: 12px;
  border-radius: 32px;
  font-weight: bold;
  box-shadow: 0px 8px 20px 0px rgba(0, 0, 0, 0.16);
}
.TodoRoutes{
  margin-left: 288px;
}
h1, h2{
  font-weight: normal;
}
.dropIcon{
  border-left: 4px solid transparent;
  border-right: 5px solid transparent;
  border-top: 5px solid;
  width: 5px;
  position: absolute;
  right: 0;
  top: 18px;
}
.btn {
  background: #8bc34a;
  border: none;
  color: #000;
  text-transform: uppercase;
  padding: 8px;
  color: #fff;
  font-weight: bold;
  cursor: pointer;
}
.card{
  box-shadow: 0px 8px 20px 0px rgba(0, 0, 0, 0.16);
  padding: 24px;
  background: #fff;
  margin: 16px;
}
.card h1{
  margin-top: 3px;
  text-align: center;
  color: #4caf50;
}
@media only screen and (max-width: 1000px){
  .menu{
    display: block !important;
  }
  .TodoRoutes{
    margin: 0;
  }
}
@media only screen and (max-width: 600px){
  .card{
    padding: 16px;
  }
  h1{
    font-size: 24px;
  }
}
.EditCreate button{
    width: 100%;
    margin-top: 10px;
}
.alert{
    font-weight: bold;
    color: #f44336;
    margin: 8px 0 0 0;
    font-size: 16px;
}
.successMessage{
    color: #fff;
    font-size: 16px;
    background: #8bc34a;
    padding: 10px 32px;
    position: fixed;
    top: 24px;
    right: 24px;
    z-index: 12;
    -webkit-animation: successMessage forwards 0.3s ease;
            animation: successMessage forwards 0.3s ease;
    box-shadow: 0px 8px 20px 0px rgba(0, 0, 0, 0.16);
}
@-webkit-keyframes successMessage{
    0%{
        opacity: 0;
    }
    100%{
        opacity: 1;
    }
}
@keyframes successMessage{
    0%{
        opacity: 0;
    }
    100%{
        opacity: 1;
    }
}
.input-field{
    cursor: text;
    position: relative;
    margin: 32px 0 0 0;
    display: block;
}
.input-field *{
    -webkit-transition: ease 0.2s all;
    transition: ease 0.2s all;
}
input{
    width: 100%;
    display: block;
    border: none;
    border-bottom: 1px solid #9e9e9e;
    height: 35px;
    font-size: 15px;
    background: transparent;
}
.input-field p{
    color: #9e9e9e;
    top: 9px;
    margin: 0;
    font-size: 15px;
    position: absolute;
}
.inputFocus p, .inputBlur p{
    top: -18px;
    font-size: 12px;
    color: #8bc34a;
}
.inputFocus input{
    border-bottom: 1.76px solid #8bc34a;
}
.inputBlur p{
    color: #9e9e9e;
}
.inputDanger input{
    border-bottom: 1px solid rgb(244, 67, 54, 0.8);
}
.inputDanger p{
    color: rgb(244, 67, 54);
    font-weight: normal;
}
input[type="date"]::-webkit-inner-spin-button,
input[type="date"]::-webkit-clear-button{
    display: none;
    -webkit-appearance: none;
}
.dateClass p{
    top: -18px !important;
    font-size: 12px;
}
input[type="date"]::-webkit-calendar-picker-indicator{
    width: 10px;
}
input[type="date"]{
    max-width: 261px;
}
header{
    padding: 36px 16px 10px 16px;
    background: -webkit-gradient(linear , left top, left bottom , from(#8bc34a) , to(#4caf50));
    background: linear-gradient(#8bc34a , #4caf50);
    box-shadow: 0px 8px 20px 0px rgba(0, 0, 0, 0.16);
}
header *{
    color: #fff;
}
.todoBox .actions *{
    padding: 12px;
    color: #fff;
    border-radius: 24px;
    box-shadow: 0px 8px 20px 0px rgba(0, 0, 0, 0.16);
    cursor: pointer;
}
.todoBox{
    background: #fff;
    margin: 16px;
    box-shadow: 0px 8px 20px 0px rgba(0, 0, 0, 0.16);
    display: flex;
    align-items: center;
    padding: 24px;
}
.check, .checked{
    border: 2px solid #8bc34a;
    padding: 1px;
    margin: 0 12px 0 8px;
    color: transparent;
    font-weight: bold;
    border-radius: 2px;
    font-size: 10px !important;
}
.todoTitle{
    cursor: pointer;
    font-size: 15px;
}
.star{
    padding: 0 !important;
    color: #8bc34a;
    margin-top: -2px;
    cursor: pointer;
}
.checked{
    background: #8bc34a;
    color: #fff;
}
.actions{
    margin-left: auto;
    display: flex;
}
.grey{
    background: #9e9e9e;
}
.red{
    background: #f44336;
}
.green{
    background: #8bc34a;
}
.marginLR{
    margin: 0 6px;
}
.expiresIn{
    -webkit-animation: linear 0.76s infinite expiresIn;
            animation: linear 0.76s infinite expiresIn;
}
@-webkit-keyframes expiresIn{
    0% {
        box-shadow: 0 0 0 0px rgb(244, 67, 54, 0.9);
      }
      100% {
        box-shadow: 0 0 0 16px rgb(244, 67, 54, 0);
      }
}
@keyframes expiresIn{
    0% {
        box-shadow: 0 0 0 0px rgb(244, 67, 54, 0.9);
      }
      100% {
        box-shadow: 0 0 0 16px rgb(244, 67, 54, 0);
      }
}
@media only screen and (max-width: 560px){
    header{
        padding: 16px 16px 6px 16px;
    }
    header h1{
        margin: 0;
    }
    .todoBox{
        padding: 16px;
    }
    .todoBox .actions *{
        padding: 10px;
        font-size: 17px;
    }
    .todoTitle{
        overflow: hidden;
        max-height: 20px;
    }
    .check, .checked{
        margin: 0 10px 0 6px;
    }
    .marginLR{
        margin: 0 3px;
    }
}
.view h3{
    margin-bottom: 8px;
}
table{
    width: 100%;
    text-align: left;
    overflow: auto;
    min-width: 520px;
    border-collapse: collapse;
}
th{
    background: rgba(225, 245, 254,1);
}
th, td{
    padding: 10px;
}
.basicInfo{
    overflow: auto;
}
.expiring{
    font-weight: bold;
    -webkit-animation: expiring 2s infinite linear;
            animation: expiring 2s infinite linear;
}
.expired{
    color: red;
}
@-webkit-keyframes expiring{
    0%{
        color: black;
    }
    30%{
        color: red;
    }
    70%{
        color: red;
    }
    100%{
        color: black;
    }
}
@keyframes expiring{
    0%{
        color: black;
    }
    30%{
        color: red;
    }
    70%{
        color: red;
    }
    100%{
        color: black;
    }
}
@media only screen and (max-width: 600px){
    th, td{
        padding: 8px;
    }
}
